import Vue from "vue";
import VueRouter  from "vue-router";
Vue.use(VueRouter)
//
import SchoolHistoryPage from '../components/SchoolHistoryPage'
import Mainpage from '../components/Mainpage.vue'
import SchoolInterface from '../components/SchoolInterface.vue'
import SchoolSceneryPage from '../components/SchoolSceneryPage.vue'
import WishPage from '../components/WishPage.vue'
export default new VueRouter({
    scrollBehavior (to, from, savedPosition) {
        return {x:0,y:0}
    },
    //
    routes:[
        //
        {
            path:'/',
            redirect:'mainpage'
        },
        {
            //
            name:'Mainpage',
            //
            path:'/mainpage',
            //
            component:Mainpage,
            // path/ to//
        },
        {
            name:'SchoolInterface',
            path:'/schoolinterface',
            component:SchoolInterface,
        },
        {
            name:'SchoolHistoryPage',
            path:'/schoolhistorypage',
            component:SchoolHistoryPage
        },
        {
            name:'SchoolSceneryPage',
            path:'/schoolscenerypage',
            component:SchoolSceneryPage
        },
        {
            name:'WishPage',
            path:'/wishpage',
            component:WishPage
        }
    ]
})