        }
    },
    components:{
        Shutter,
        Leftmenu,
        Bottomimgs,
        Season
    },
    //
    beforeMount(){
        let name = this.$route.query.schoolid
        console.log('beforemount'+this.name)
        if(name==''){
            this.tempdata=this.alldata.zheda
        }else if(name==''){
            this.tempdata=this.alldata.beida
        }else if(name==''){
            this.tempdata=this.alldata.wuda
        }else if(name==''){
            this.tempdata=this.alldata.zhengda
        }
    },
    mounted() {
        if(this.showShutter==false){
            this.showShutter=true
        }
        let that=this
        this.cycle=setInterval(() => {
            if(that.bg1==true){
                that.bg1=false
                setTimeout(() => {
                that.bg2=true
                }, 800);
            }
            else if(that.bg2==true){
                that.bg2=false
                setTimeout(() => {
                that.bg3=true
                }, 800);
            }
            else if(that.bg3==true){
                that.bg3=false
                setTimeout(() => {
                that.bg4=true
                }, 800);
            }
            else if(that.bg4==true){
                that.bg4=false
                setTimeout(() => {
                that.bg1=true
                }, 800);
            }
        }, 6000);    
    },
    beforeDestroy(){
        clearInterval(this.cycle)
    }
}
</script>
<style scoped>
.bottomimgs{
    position: absolute;
    left: 0px;
    bottom: 20px;
}
.bgdiv{
    opacity: 80%;
    position:fixed;
    z-index: -1;
    top: 60px;
    height: 692px;
    width: 100%;
    left: 40px;
}
.bgimg{
    width: 100%;
    height: 100%;
}
.schoolSceneryroot{
    height: 1950px;
    width: 100%;
    display: relative;
    overflow: hidden;
}
.season{
    margin-left: 3%;
    position: relative;
    margin-top: 10%;
}
</style>
